import LoadingImg from '../../public/loadingImg'
import Loading from '../../public/Loading'
  export default {
      name: "refund",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          index:1,
          value:1,
          house:[],
          options:[
            {label:'全退',value:1},
            {label:'非全退',value:0},
          ],
          reson:'',
          orderInformation:'',
          pay_refund:'',
          balance:'',
          total_price:"",
          order_id:'',
          isLoading:false,
          returnFace:'',
          disabled:false,
          combineL:[],
          adult_num:0,
          kids_num:0,
          person_a:0,
          person_k:0
        }
    },
    components:{
      Loading,
      LoadingImg
    },
    computed:{
      isChoose(item,index){
        return function(item,index){
          if(this.combineL.indexOf(item.combine_id) != -1){
              return true
          }else{
            return false
          }
        }
      },
    },
    watch:{
      value:function(){
        this.getHouseNum()
      },
      

    },
    methods:{
      chooseCombine(item,index){
        if(this.combineL.indexOf(item.combine_id)  == -1){
          this.combineL.push(item.combine_id)
        }else{
          let i  = this.combineL.indexOf(item.combine_id)
           this.combineL.splice(i,1)
        }
        this.getHouseNum()
      },
      goBack(){
        if(this.index == 2){
          this.$router.push('/transaction')
        }else{
          this.$router.go(-1)
        }
      },
      reduceP(val){
        if(val == 1){
          this.person_a -= 1
          this.getHouseNum()
        }else{
          this.person_a += 1
          this.getHouseNum()
        }
      },
      reduceK(val){
        if(val == 1){
          this.person_k -= 1
          this.getHouseNum()
        }else{
          this.person_k += 1
          this.getHouseNum()
        }
      },
      reduceH(item,val){
        if(val == 1){
          item.number -= 1
          this.getHouseNum()
        }else{
          item.number += 1
          this.getHouseNum()
        }
      },
      getHouseNum(){
         let b= []
          for(let i =0;i<this.orderInformation.house.length;i++){
            if(this.orderInformation.house[i].number){
            b.push({oh_id:this.orderInformation.house[i].oh_id,num:this.orderInformation.house[i].number})

            }
          }
          this.getAmount(JSON.stringify(b))
      },
      sendRefun(){
        this.isLoading = true
          let b = []
          for(let i =0;i<this.orderInformation.house.length;i++){
             if(this.orderInformation.house[i].number){
            b.push({oh_id:this.orderInformation.house[i].oh_id,num:this.orderInformation.house[i].number})

            }
          }
          this.$http.post(this.api + '/RefundSTwo',{
            token: localStorage.getItem('token'),
            verson:2.0,
            order_id: this.order_id,
            person_num: this.value? this.adult_num:this.person_a,
            house: this.value?'':JSON.stringify(b),
            kids_num: this.value?this.kids_num:this.person_k,
            combine: this.value? '': JSON.stringify(this.combineL),
            flag: this.value,
            reason: this.reson,
            type: 0
          })
          .then(res=>{
            if(res.data.code == 1){
              this.isLoading = false
              if(this.pay_refund){
                this.index = 2
              }else{
                this.$message({
                  type:'success',
                  message:'您的退款将会根据该活动退款政策在2-3个工作日进行退款！'
                })
              }
              this.$router.push('/refundOrder')
            }else if(res.data.code == 3){
              this.sendRefun()
            }else if(res.data.code == 0){
              this.isLoading = false
              this.$message({type:'error',message:res.data.msg})
            }
          })
        
      },
      checkRefund(){
        this.$http.post(this.api + '/CheckPolicy',{
          token: localStorage.getItem('token'),
          order_id: this.order_id,
        })
        .then(res=>{
          if(res.data.code == 1){
            this.pay_refund = 1
          }else if(res.data.code == 3){
             this.$http.post(this.api + '/home/index/token')
              .then(res=>{
                localStorage.setItem('token',res.data.data)
                this.checkRefund()
              })
          }else{
            this.pay_refund = 0
          }
        })
      },
      getAmount(house){
        this.$http.post(this.api + '/RefundAmountTwo',{
          token: localStorage.getItem('token'),
          verson:2.0,
          order_id:this.order_id,
          person_num:this.value ==0?this.person_a:'',
          house:this.value ==0?house:'',
          reason:this.reason,
          kids_num:this.value ==0? this.person_k:'',
          combine: this.value ==0?JSON.stringify(this.combineL):'',
          flag:this.value
        })
          .then(res=>{
            if(res.data.code == 1){
              this.balance = res.data.data.balance
              this.total_price = (res.data.data.pay_price).toFixed(2)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getAmount(order,person,house,reason,flag)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      getOrderMation(){
        this.$http.post(this.api + '/OrderRefundDTwo',{
          token: localStorage.getItem('token'),
          order_id: this.order_id,
        })
          .then(res=>{
            if(res.data.code == 1){

              let data  = res.data.data
              console.log(data)
              for(var i =0;i<data.detail.length;i++){
                
                if(data.detail[i].type == '0'){
                  this.adult_num = data.detail[i].adult 
                  this.kids_num =  data.detail[i].kids
                }
              }
              for(var i = 0; i < data.house.length;  i++){
                data.house[i].number = 0
              }
              this.orderInformation = data
              console.log(this.orderInformation)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getOrderMation()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
    },
    mounted(){
        this.getOrderMation()
      this.checkRefund()
      this.getAmount()
    },
    created(){
      this.order_id = this.$route.query.information
      this.returnFace = this.$route.query.returnFace
      if(this.returnFace){
        this.disabled = true
        this.value = 0
      }else{
        this.disabled = false
        this.value = 1
      }
    }
  }



